import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useUser } from '../shared/contexts/UserContext';
import { API_BASE_URL } from '../shared/constants/config'; // Add this import
import "./Navbar.css";
import { Home, Info, Calendar, Mail, Heart, User, LogOut, BookOpen } from "lucide-react";
import NotificationBell from "./NotificationBell";

function Navbar() {
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const { user, signOut, refreshUser } = useUser();
  const navigate = useNavigate();

  const toggleDropdown = () => setDropdownOpen((prev) => !prev);

  const handleSignOut = () => {
    signOut();
    navigate("/signin");
  };

  // Refresh user data on load to get updated profilePic
  useEffect(() => {
    if (user) refreshUser();
  }, [user, refreshUser]);

  if (!user || user.role !== "user") return null; // hide navbar if not user

  // Normalize profilePic URL - FIXED!
  const profilePicUrl = user.profilePic
    ? user.profilePic.startsWith("http")
      ? user.profilePic
      : `${API_BASE_URL}/${user.profilePic.replace(/\\/g, "/")}` // Changed!
    : "/default-avatar.png";

  return (
    <nav className="navbar">
      {/* Logo Section - Left Side */}
      <div className="navbar-left">
        <div className="navbar-logo"> Student-Led-Initiative</div>
      </div>

      {/* Main Navigation Links - Center */}
      <div className="navbar-center">
        <Link to="/home" className="nav-link">
          <Home size={20} />
          <span>Home</span>
        </Link>
        <Link to="/about" className="nav-link">
          <Info size={20} />
          <span>About</span>
        </Link>
        <Link to="/story" className="nav-link">
          <BookOpen size={20} />
          <span>Our Story</span>
        </Link>
        <Link to="/events" className="nav-link">
          <Calendar size={20} />
          <span>Events</span>
        </Link>
        <Link to="/apply" className="nav-link">
          <Heart size={20} />
          <span>Apply Medical Support</span>
        </Link>
      </div>

      {/* Profile Dropdown - Right Side */}
      <div className="navbar-right">
        {/*  Added Notification Bell here */}
        <NotificationBell userRole={user.role} />

        <img
          src={profilePicUrl}
          alt="Profile"
          className="navbar-profile-pic"
          onClick={toggleDropdown}
        />

        {/* Dropdown Menu */}
        <div className={`dropdown-menu ${dropdownOpen ? "open" : ""}`}>
          <Link to="/profile" className="dropdown-link" onClick={() => setDropdownOpen(false)}>
            <User size={20} />
            <span>Profile</span>
          </Link>

          <Link to="/contact" className="dropdown-link" onClick={() => setDropdownOpen(false)}>
            <Mail size={20} />
            <span>Contact</span>
          </Link>

          <div className="dropdown-divider"></div>

          <button onClick={handleSignOut} className="dropdown-link logout-btn">
            <LogOut size={20} />
            <span>Sign Out</span>
          </button>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;