import React, { useState, useEffect } from "react";
import { useUser } from "../../shared/contexts/UserContext";
import { staffAPI } from "../../shared/services/api.js";
import { User, Mail, Camera, Check, X, Loader } from "lucide-react";
import "./StaffProfile.css";

function StaffProfile() {
  const { user, setUser, refreshUser } = useUser();
  const [profile, setProfile] = useState({});
  const [image, setImage] = useState(null);
  const [preview, setPreview] = useState(null);
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);

  // Initialize profile state
  useEffect(() => {
    if (!user) return;

    const fetchLatestProfile = async () => {
      try {
        await refreshUser(); // Refresh from backend
      } catch (err) {
        console.error("Failed to refresh profile:", err.message);
      }
    };

    fetchLatestProfile();

    setProfile({
      name: user.name,
      email: user.email,
      profilePic: user.profilePic,
    });
  }, [user, refreshUser]);

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    // Validate file size
    if (file.size > 5 * 1024 * 1024) {
      setMessage(" Image size should be less than 5MB");
      setTimeout(() => setMessage(""), 3000);
      return;
    }

    // Validate file type
    const validTypes = ["image/jpeg", "image/jpg", "image/png", "image/gif", "image/webp"];
    if (!validTypes.includes(file.type)) {
      setMessage(" Please upload a valid image file (JPG, PNG, GIF, or WebP)");
      setTimeout(() => setMessage(""), 3000);
      return;
    }

    setImage(file);
    setPreview(URL.createObjectURL(file));
    setMessage("");
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!user) return setMessage(" Staff not found.");

    setLoading(true);

    const formData = new FormData();
    if (image) formData.append("profilePic", image);
    formData.append("name", profile.name);
    formData.append("email", profile.email);

    try {
      const res = await staffAPI.updateProfile(user.id, formData);

      const updatedStaff = res.data.staff;

      const profilePicUrl = updatedStaff.profilePic
        ? `${updatedStaff.profilePic.replace(/\\/g, "/")}?t=${Date.now()}`
        : null;

      const updatedUser = { ...user, ...updatedStaff, profilePic: profilePicUrl };

      setUser(updatedUser);
      sessionStorage.setItem("user", JSON.stringify(updatedUser));

      setProfile({ ...profile, profilePic: profilePicUrl });
      setPreview(null);
      setImage(null);
      setMessage(" Profile updated successfully!");
      setTimeout(() => setMessage(""), 3000);
    } catch (err) {
      console.error(" Failed to update staff profile:", err);

      if (err.response?.status === 401) {
        setMessage(" Unauthorized. Please log in again.");
      } else if (err.response?.status === 400) {
        setMessage(" Invalid data. Please check your inputs.");
      } else {
        setMessage(" Failed to update profile. Please try again.");
      }

      setTimeout(() => setMessage(""), 4000);
    } finally {
      setLoading(false);
    }
  };

  const removePreview = () => {
    setPreview(null);
    setImage(null);
  };

  const handleCancel = () => {
    setProfile({
      name: user.name,
      email: user.email,
      profilePic: user.profilePic,
    });
    setPreview(null);
    setImage(null);
    setMessage("");
  };

  if (!user) {
    return (
      <div className="profile-error">
        <User size={48} />
        <p>Please sign in to view your profile.</p>
      </div>
    );
  }

  return (
    <div className="staff-profile-page">
      <div className="profile-hero">
        <div className="hero-content">
          <h1>Staff Profile</h1>
          <p>Manage your personal information and preferences</p>
        </div>
      </div>

      <div className="profile-container">
        {message && (
          <div className={`alert ${message.startsWith("") ? "alert-success" : "alert-error"}`}>
            {message.startsWith("") ? <Check size={20} /> : <X size={20} />}
            <span>{message}</span>
          </div>
        )}

        <div className="profile-card">
          <form onSubmit={handleSubmit}>
            {/* Profile Picture */}
            <div className="profile-picture-section">
              <div className="picture-wrapper">
                <div className="profile-pic-container">
                  {preview ? (
                    <img src={preview} alt="Preview" className="profile-pic" />
                  ) : profile.profilePic ? (
                    <img src={profile.profilePic} alt="Profile" className="profile-pic" />
                  ) : (
                    <div className="profile-pic-placeholder">
                      <User size={64} />
                    </div>
                  )}
                  <label htmlFor="profile-pic-upload" className="camera-button" title="Upload profile picture">
                    <Camera size={20} />
                    <input
                      id="profile-pic-upload"
                      type="file"
                      accept="image/*"
                      onChange={handleImageChange}
                      style={{ display: "none" }}
                    />
                  </label>
                </div>
                {preview && (
                  <button type="button" className="remove-preview-btn" onClick={removePreview}>
                    <X size={16} /> Remove
                  </button>
                )}
              </div>
              <div className="picture-info">
                <h3>{profile.name || "Staff Member"}</h3>
                <p className="role-badge">Staff</p>
                <p className="upload-hint">Click the camera icon to upload a new picture (Max 5MB)</p>
              </div>
            </div>

            {/* Form Fields */}
            <div className="form-section">
              <h3>Personal Information</h3>

              <div className="form-group">
                <label htmlFor="name">
                  <User size={18} /> Full Name
                </label>
                <input
                  id="name"
                  type="text"
                  value={profile.name || ""}
                  onChange={(e) => setProfile({ ...profile, name: e.target.value })}
                  placeholder="Enter your full name"
                  required
                  minLength={2}
                  maxLength={50}
                />
              </div>

              <div className="form-group">
                <label htmlFor="email">
                  <Mail size={18} /> Email Address
                </label>
                <input
                  id="email"
                  type="email"
                  value={profile.email || ""}
                  onChange={(e) => setProfile({ ...profile, email: e.target.value })}
                  placeholder="Enter your email"
                  required
                />
              </div>
            </div>

            {/* Action Buttons */}
            <div className="form-actions">
              <button type="button" className="btn-cancel" onClick={handleCancel} disabled={loading}>
                <X size={18} /> Cancel
              </button>
              <button type="submit" className="btn-submit" disabled={loading}>
                {loading ? (
                  <>
                    <Loader size={18} className="spinner" /> Updating...
                  </>
                ) : (
                  <>
                    <Check size={18} /> Update Profile
                  </>
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default StaffProfile;
