import React, { useEffect, useRef, useState } from "react";
import { initializeSocket } from "../../shared/services/socketService";
import "./Contact.css";

export default function Contact() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");
  const [socket, setSocket] = useState(null);
  const [isConnected, setIsConnected] = useState(false);
  const chatEndRef = useRef(null);

  const quickTopics = [
    "Payment Issues",
    "Technical Support",
    "Account Help",
    "Medical Support",
    "Other Queries",
  ];

  const [callForm, setCallForm] = useState({
    phone: "",
    language: "English",
    bestTime: "As soon as possible",
    notes: "",
  });
  const [feedbackForm, setFeedbackForm] = useState({
    rating: "",
    suggestion: "",
  });
  const [callSubmitting, setCallSubmitting] = useState(false);
  const [feedbackSubmitting, setFeedbackSubmitting] = useState(false);
  const [callSuccess, setCallSuccess] = useState("");
  const [callDone, setCallDone] = useState(false);
  const [feedbackSubmitted, setFeedbackSubmitted] = useState(false);
  const [showThankYou, setShowThankYou] = useState(false);
  const [callRequestId, setCallRequestId] = useState(null);

  // Initialize socket connection using centralized service
  useEffect(() => {
    console.log(" Initializing socket connection from Contact page...");
    
    // Get or initialize the socket
    const socketInstance = initializeSocket();
    setSocket(socketInstance);

    const handleConnect = () => {
      console.log(" Socket connected in Contact! ID:", socketInstance.id);
      setIsConnected(true);
      socketInstance.emit("registerRole", "user");
      console.log(" Registered as user");
    };

    const handleConnectError = (error) => {
      console.error(" Socket connection error:", error);
      setIsConnected(false);
    };

    const handleDisconnect = (reason) => {
      console.log(" Socket disconnected:", reason);
      setIsConnected(false);
    };

    // Set up event listeners
    socketInstance.on("connect", handleConnect);
    socketInstance.on("connect_error", handleConnectError);
    socketInstance.on("disconnect", handleDisconnect);

    // If already connected, trigger connect handler
    if (socketInstance.connected) {
      handleConnect();
    }

    return () => {
      // Don't close the socket, just remove listeners
      socketInstance.off("connect", handleConnect);
      socketInstance.off("connect_error", handleConnectError);
      socketInstance.off("disconnect", handleDisconnect);
    };
  }, []);

  // Restore state from sessionStorage on mount
  useEffect(() => {
    if (sessionStorage.getItem("callDone") === "true") setCallDone(true);
    if (sessionStorage.getItem("feedbackSubmitted") === "true") setFeedbackSubmitted(true);

    const storedId = sessionStorage.getItem("callRequestId");
    if (storedId) setCallRequestId(storedId);
  }, []);

  // Persist state to sessionStorage
  useEffect(() => {
    sessionStorage.setItem("callDone", callDone);
    sessionStorage.setItem("feedbackSubmitted", feedbackSubmitted);
    if (callRequestId) sessionStorage.setItem("callRequestId", callRequestId);
    else sessionStorage.removeItem("callRequestId");
  }, [callDone, feedbackSubmitted, callRequestId]);

  // Scroll to bottom on new messages
  useEffect(() => {
    chatEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  // Receive staff messages
  useEffect(() => {
    if (!socket) return;

    const handleStaffReply = (msg) => {
      console.log(" Received staff reply:", msg);
      setMessages((prev) => [...prev, { ...msg, role: "staff", id: Date.now() }]);
    };

    socket.on("staffReply", handleStaffReply);
    return () => socket.off("staffReply", handleStaffReply);
  }, [socket]);

  // Handle call deletion from server
  useEffect(() => {
    if (!socket) return;

    const handleCallDeleted = ({ id }) => {
      const storedId = callRequestId || sessionStorage.getItem("callRequestId");
      console.log(" callDeleted event received");
      console.log("   Deleted ID:", id);
      console.log("   Our ID:", storedId);
      
      if (id === storedId) {
        console.log(" Resetting form - our call was deleted");
        
        // Reset all states immediately
        setCallRequestId(null);
        setCallDone(false);
        setCallSuccess("");
        setFeedbackSubmitted(false);
        setShowThankYou(false);
        setFeedbackForm({ rating: "", suggestion: "" });
        
        // Clear session storage
        sessionStorage.removeItem("callDone");
        sessionStorage.removeItem("feedbackSubmitted");
        sessionStorage.removeItem("callRequestId");
        
        console.log(" Form reset complete");
      }
    };

    const handleCallUpdated = (updatedCall) => {
      const storedId = callRequestId || sessionStorage.getItem("callRequestId");
      console.log(" callUpdated event received:", updatedCall);
      
      if ((updatedCall.id === storedId || updatedCall._id === storedId) && updatedCall.status === "completed") {
        console.log(" Call completed - cleaning up after delay");
        
        // Show thank you briefly, then reset
        setShowThankYou(true);
        setFeedbackSubmitted(true);
        
        setTimeout(() => {
          setCallRequestId(null);
          setCallDone(false);
          setCallSuccess("");
          setFeedbackSubmitted(false);
          setShowThankYou(false);
          setFeedbackForm({ rating: "", suggestion: "" });
          
          sessionStorage.removeItem("callDone");
          sessionStorage.removeItem("feedbackSubmitted");
          sessionStorage.removeItem("callRequestId");
          
          console.log(" Form reset after completion");
        }, 3000);
      }
    };

    console.log(" Listening for callDeleted and callUpdated events");
    socket.on("callDeleted", handleCallDeleted);
    socket.on("callUpdated", handleCallUpdated);
    
    return () => {
      socket.off("callDeleted", handleCallDeleted);
      socket.off("callUpdated", handleCallUpdated);
    };
  }, [socket, callRequestId]);

  const sendMessage = (text) => {
    if (!text.trim()) {
      console.log(" Empty message, not sending");
      return;
    }

    if (!socket) {
      console.error(" Socket is null!");
      alert("Socket not initialized. Please refresh the page.");
      return;
    }

    if (!isConnected) {
      console.error(" Socket not connected!");
      alert("Not connected to server. Please wait or refresh.");
      return;
    }

    console.log(" Sending message:", text);
    console.log(" Socket ID:", socket.id);
    console.log(" Socket connected:", socket.connected);
    
    const userMsg = { id: Date.now() + "-u", role: "user", text: text.trim() };
    setMessages((m) => [...m, userMsg]);
    
    socket.emit("userMessage", { text: text.trim() });
    console.log(" userMessage event emitted");
    
    setInput("");
  };

  const handleSend = (e) => {
    e.preventDefault();
    sendMessage(input);
  };

  const handleTopicClick = (topic) => sendMessage(topic);

  const handleCallChange = (e) => {
    setCallSuccess("");
    const { name, value } = e.target;
    setCallForm((f) => ({ ...f, [name]: value }));
  };

  const handleFeedbackChange = (e) => {
    const { name, value } = e.target;
    setFeedbackForm((f) => ({ ...f, [name]: value }));
  };

  const submitCallRequest = async (e) => {
    e.preventDefault();

    if (!socket) {
      console.error(" Socket is null!");
      setCallSuccess(" Not connected. Please refresh the page.");
      return;
    }

    if (!isConnected) {
      console.error(" Socket not connected!");
      setCallSuccess(" Not connected to server. Please refresh the page.");
      return;
    }

    setCallSubmitting(true);
    setCallSuccess("");
    setFeedbackSubmitted(false);

    console.log(" Submitting call request via socket");
    console.log(" Socket ID:", socket.id);
    console.log(" Socket connected:", socket.connected);
    console.log(" Call data:", callForm);

    try {
      socket.emit(
        "callRequest",
        { ...callForm, name: "User", status: "pending" },
        (response) => {
          console.log(" Call request callback received:", response);
          
          if (response && response._id) {
            console.log(" Call request successful, ID:", response._id);
            setCallRequestId(response._id);
            setCallSuccess(" Request submitted. You'll receive a callback shortly.");
            setCallForm({
              phone: "",
              language: "English",
              bestTime: "As soon as possible",
              notes: "",
            });
            setCallDone(true);
          } else if (response && response.error) {
            console.error(" Call request error:", response.error);
            setCallSuccess(" " + response.error);
          } else {
            console.warn(" Unexpected response:", response);
            setCallSuccess(" Request submitted. You'll receive a callback shortly.");
            setCallDone(true);
          }
        }
      );
      console.log(" callRequest event emitted");
    } catch (error) {
      console.error(" Error emitting call request:", error);
      setCallSuccess(" Something went wrong. Please try again.");
    } finally {
      setCallSubmitting(false);
    }
  };

  const submitFeedback = async (e) => {
    e.preventDefault();
    if (!feedbackForm.rating || !feedbackForm.suggestion.trim()) {
      alert(" Please fill in all feedback fields.");
      return;
    }

    if (!socket || !isConnected) {
      alert(" Not connected to server.");
      return;
    }

    setFeedbackSubmitting(true);

    console.log(" Submitting feedback:", feedbackForm);

    try {
      socket.emit("feedbackSubmit", { ...feedbackForm, callId: callRequestId });

      setFeedbackForm({ rating: "", suggestion: "" });
      setCallDone(false);
      setFeedbackSubmitted(true);
      setShowThankYou(true);

      setTimeout(() => {
        setShowThankYou(false);
        setFeedbackSubmitted(false);
        setCallRequestId(null);
        setCallDone(false);
        setCallSuccess("");
        sessionStorage.removeItem("callRequestId");
      }, 3000);
    } catch (error) {
      console.error(" Error submitting feedback:", error);
      alert(" Failed to submit feedback.");
    } finally {
      setFeedbackSubmitting(false);
    }
  };

  return (
    <div className="contact-page">
      <section className="contact-hero">
        <div className="hero-left">
          <h1>Help Center</h1>
          <p>We&apos;re here to helpchat with us or request a quick callback.</p>
          <div style={{ fontSize: '12px', marginTop: '10px', color: isConnected ? '#4caf50' : '#f44336' }}>
            {isConnected ? ' Connected' : ' Disconnected'}
          </div>
        </div>
      </section>

      <section className="contact-grid">
        <div className="card chat-card">
          <div className="card-header">
            <div className="title"> Chat with Staff</div>
            <div className="badge">Live</div>
          </div>

          <div className="chat-window">
            {messages.map((m) => (
              <div key={m.id} className={`bubble ${m.role === "user" ? "me" : "staff"}`}>
                <span className="bubble-text">{m.text}</span>
              </div>
            ))}
            <div ref={chatEndRef} />
          </div>

          <div className="quick-topics">
            {quickTopics.map((t) => (
              <button key={t} className="chip" onClick={() => handleTopicClick(t)}>
                {t}
              </button>
            ))}
          </div>

          <form className="chat-input" onSubmit={handleSend}>
            <input
              placeholder="Type your message"
              value={input}
              onChange={(e) => setInput(e.target.value)}
              disabled={!isConnected}
            />
            <button type="submit" className="send-btn wide" disabled={!isConnected || !input.trim()}>
              Send
            </button>
          </form>
          <div className="transfer-note">
            A staff member will reply here in chat as soon as possible.
          </div>
        </div>

        <div className="card call-card">
          <div className="card-header">
            <div className="title"> Request a Call</div>
            <div className="badge outline">25 min</div>
          </div>

          {!feedbackSubmitted && !showThankYou && !callRequestId && (
            <form className="form" onSubmit={submitCallRequest}>
              <label htmlFor="phone">Phone number</label>
              <input
                id="phone"
                name="phone"
                type="tel"
                placeholder="e.g. 98765 43210"
                value={callForm.phone}
                onChange={handleCallChange}
                required
              />

              <label htmlFor="language">Preferred language</label>
              <select
                id="language"
                name="language"
                value={callForm.language}
                onChange={handleCallChange}
              >
                <option>English</option>
                <option>Hindi</option>
                <option>Kannada</option>
                <option>Telugu</option>
                <option>Tamil</option>
                <option>Marathi</option>
              </select>

              <label htmlFor="bestTime">Preferred time</label>
              <select
                id="bestTime"
                name="bestTime"
                value={callForm.bestTime}
                onChange={handleCallChange}
              >
                <option>As soon as possible</option>
                <option>Morning (9am12pm)</option>
                <option>Afternoon (12pm4pm)</option>
                <option>Evening (4pm8pm)</option>
              </select>

              <label htmlFor="notes">Notes</label>
              <textarea
                id="notes"
                name="notes"
                placeholder="Briefly describe your issue"
                rows={3}
                value={callForm.notes}
                onChange={handleCallChange}
              />

              <button className="primary" disabled={callSubmitting || !isConnected}>
                {callSubmitting ? "Submitting" : "Request a Callback"}
              </button>
            </form>
          )}

          {callSuccess && !feedbackSubmitted && !showThankYou && (
            <div className="status">{callSuccess}</div>
          )}

          {callDone && callRequestId && !feedbackSubmitted && !showThankYou && (
            <form className="form feedback-form" onSubmit={submitFeedback}>
              <h4>Feedback</h4>

              <label htmlFor="rating">Rating: How staff helped you</label>
              <select
                id="rating"
                name="rating"
                value={feedbackForm.rating}
                onChange={handleFeedbackChange}
                required
              >
                <option value="">-- Select rating --</option>
                <option>Excellent</option>
                <option>Good</option>
                <option>Average</option>
                <option>Poor</option>
              </select>

              <label htmlFor="suggestion">Suggestions for betterment</label>
              <textarea
                id="suggestion"
                name="suggestion"
                placeholder="Your suggestions"
                rows={3}
                value={feedbackForm.suggestion}
                onChange={handleFeedbackChange}
                required
              />

              <div style={{ display: 'flex', gap: '10px' }}>
                <button 
                  className="primary" 
                  disabled={feedbackSubmitting}
                  style={{ flex: 1 }}
                >
                  {feedbackSubmitting ? "Submitting" : "Submit Feedback"}
                </button>
                <button 
                  type="button"
                  onClick={() => {
                    // Clear everything and return to call request form
                    setCallRequestId(null);
                    setCallDone(false);
                    setCallSuccess("");
                    setFeedbackForm({ rating: "", suggestion: "" });
                    sessionStorage.removeItem("callDone");
                    sessionStorage.removeItem("callRequestId");
                  }}
                  style={{
                    padding: '10px 20px',
                    background: '#6c757d',
                    color: 'white',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: 'pointer',
                    fontWeight: 'bold'
                  }}
                >
                  Cancel
                </button>
              </div>
            </form>
          )}

          {showThankYou && <div className="status"> Thank you for your feedback!</div>}
        </div>
      </section>
    </div>
  );
}