// frontend/src/components/StaffVerification.jsx
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Search, FileText, CheckCircle, XCircle, Eye, Download, Filter } from "lucide-react";
import "./StaffVerification.css";

const StaffVerification = () => {
  const [applications, setApplications] = useState([]);
  const [filteredApps, setFilteredApps] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState("all");
  const [selectedApp, setSelectedApp] = useState(null);
  const [selectedStat, setSelectedStat] = useState(null);
  const API_URL = process.env.REACT_APP_API_URL || "http://localhost:5000";
  const navigate = useNavigate();

  const token = localStorage.getItem("token");

  useEffect(() => {
    if (!token) {
      alert("Please log in first");
      navigate("/login");
    }
  }, [token, navigate]);

  useEffect(() => {
    const fetchApplications = async () => {
      try {
        const res = await fetch(`${API_URL}/api/medical/pending`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (res.status === 401) {
          alert("Session expired or unauthorized. Please log in again.");
          localStorage.removeItem("token");
          navigate("/login");
          return;
        }

        if (!res.ok) throw new Error("Failed to fetch applications");
        const data = await res.json();
        setApplications(data);
        setFilteredApps(data);
      } catch (err) {
        console.error("Error fetching applications:", err);
        alert("Failed to fetch applications. Check console for details.");
      }
    };

    if (token) fetchApplications();
  }, [API_URL, token, navigate]);

  useEffect(() => {
    const term = searchTerm.toLowerCase();
    let filtered = applications.filter(
      (app) =>
        (app.applicantName?.toLowerCase().includes(term) ||
          app.staffName?.toLowerCase().includes(term) ||
          app.patientName?.toLowerCase().includes(term))
    );

    if (statusFilter !== "all") {
      filtered = filtered.filter((app) => app.status === statusFilter);
    }

    setFilteredApps(filtered);
  }, [searchTerm, statusFilter, applications]);

  const handleAction = async (id, status) => {
    try {
      const res = await fetch(`${API_URL}/api/medical/verify/${id}`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({ status }),
      });
      if (!res.ok) throw new Error("Failed to update application");
      const updated = await res.json();

      setApplications((prev) =>
        prev.map((app) => (app._id === id ? updated.application : app))
      );
      setFilteredApps((prev) =>
        prev.map((app) => (app._id === id ? updated.application : app))
      );
      alert(`Application ${status} successfully!`);
    } catch (err) {
      console.error(err);
      alert("Action failed");
    }
  };

  const stats = {
    total: applications.length,
    pending: applications.filter(a => a.status === "pending").length,
    verified: applications.filter(a => a.status === "verified").length,
    rejected: applications.filter(a => a.status === "rejected").length,
  };

  const getStatDetails = (statType) => {
    let filteredData = [];
    let title = "";
    
    switch(statType) {
      case 'total':
        filteredData = applications;
        title = "All Applications";
        break;
      case 'pending':
        filteredData = applications.filter(a => a.status === "pending");
        title = "Pending Applications";
        break;
      case 'verified':
        filteredData = applications.filter(a => a.status === "verified");
        title = "Verified Applications";
        break;
      case 'rejected':
        filteredData = applications.filter(a => a.status === "rejected");
        title = "Rejected Applications";
        break;
      default:
        filteredData = applications;
        title = "Applications";
    }
    
    return { filteredData, title };
  };

  return (
    <div className="verification-page">
      {/* Hero Section */}
      <div className="verification-hero">
        <div className="hero-content">
          <h1> Application Verification</h1>
          <p>Review and verify medical fund applications</p>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="stats-container">
        <div className="stat-card total clickable" onClick={() => setSelectedStat('total')}>
          <div className="stat-icon"></div>
          <div className="stat-content">
            <div className="stat-value">{stats.total}</div>
            <div className="stat-label">Total Applications</div>
          </div>
        </div>
        <div className="stat-card pending clickable" onClick={() => setSelectedStat('pending')}>
          <div className="stat-icon"></div>
          <div className="stat-content">
            <div className="stat-value">{stats.pending}</div>
            <div className="stat-label">Pending Review</div>
          </div>
        </div>
        <div className="stat-card verified clickable" onClick={() => setSelectedStat('verified')}>
          <div className="stat-icon"></div>
          <div className="stat-content">
            <div className="stat-value">{stats.verified}</div>
            <div className="stat-label">Verified</div>
          </div>
        </div>
        <div className="stat-card rejected clickable" onClick={() => setSelectedStat('rejected')}>
          <div className="stat-icon"></div>
          <div className="stat-content">
            <div className="stat-value">{stats.rejected}</div>
            <div className="stat-label">Rejected</div>
          </div>
        </div>
      </div>

      {/* Filters Section */}
      <div className="filters-section">
        <div className="search-box">
          <Search size={20} className="search-icon" />
          <input
            type="text"
            placeholder="Search by applicant, staff, or patient name..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
        </div>

        <div className="filter-buttons">
          <Filter size={18} />
          <button
            className={`filter-btn ${statusFilter === "all" ? "active" : ""}`}
            onClick={() => setStatusFilter("all")}
          >
            All ({applications.length})
          </button>
          <button
            className={`filter-btn ${statusFilter === "pending" ? "active" : ""}`}
            onClick={() => setStatusFilter("pending")}
          >
            Pending ({stats.pending})
          </button>
          <button
            className={`filter-btn ${statusFilter === "verified" ? "active" : ""}`}
            onClick={() => setStatusFilter("verified")}
          >
            Verified ({stats.verified})
          </button>
          <button
            className={`filter-btn ${statusFilter === "rejected" ? "active" : ""}`}
            onClick={() => setStatusFilter("rejected")}
          >
            Rejected ({stats.rejected})
          </button>
        </div>
      </div>

      {/* Applications Table */}
      <div className="table-container">
        <div className="table-wrapper">
          <table className="verification-table">
            <thead>
              <tr>
                <th>Applicant</th>
                <th>Patient Name</th>
                <th>Hospital</th>
                <th>Status</th>
                <th>Applied Date</th>
                <th>Documents</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {filteredApps.length > 0 ? (
                filteredApps.map((app) => (
                  <tr key={app._id}>
                    <td>
                      <div className="applicant-cell">
                        <div className="applicant-name">{app.applicantName || app.staffName}</div>
                      </div>
                    </td>
                    <td className="patient-name">{app.patientName}</td>
                    <td className="hospital-name">{app.hospital}</td>
                    <td>
                      <span className={`status-badge status-${app.status}`}>
                        {app.status === "pending" && <span></span>}
                        {app.status === "verified" && <span></span>}
                        {app.status === "rejected" && <span></span>}
                        {app.status}
                      </span>
                    </td>
                    <td className="date-cell">
                      {new Date(app.createdAt).toLocaleDateString("en-US", {
                        month: "short",
                        day: "numeric",
                        year: "numeric",
                      })}
                    </td>
                    <td>
                      <button
                        className="btn-view"
                        onClick={() => setSelectedApp(app)}
                      >
                        <Eye size={16} />
                        View Docs
                      </button>
                    </td>
                    <td>
                      <div className="action-buttons">
                        <button
                          className="btn-verify"
                          onClick={() => handleAction(app._id, "verified")}
                          disabled={app.status !== "pending"}
                          title="Verify Application"
                        >
                          <CheckCircle size={16} />
                          Verify
                        </button>
                        <button
                          className="btn-reject"
                          onClick={() => handleAction(app._id, "rejected")}
                          disabled={app.status !== "pending"}
                          title="Reject Application"
                        >
                          <XCircle size={16} />
                          Reject
                        </button>
                      </div>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="7" className="no-data">
                    <div className="no-data-content">
                      <FileText size={48} className="no-data-icon" />
                      <p>No applications found</p>
                      <span>Try adjusting your search or filters</span>
                    </div>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Document Modal */}
      {selectedApp && (
        <div className="modal-overlay" onClick={() => setSelectedApp(null)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3>
                <FileText size={24} />
                Uploaded Documents
              </h3>
              <button
                className="modal-close"
                onClick={() => setSelectedApp(null)}
              >
                
              </button>
            </div>

            <div className="modal-body">
              <div className="applicant-info">
                <div className="info-row">
                  <strong>Applicant:</strong>
                  <span>{selectedApp.applicantName || selectedApp.staffName}</span>
                </div>
                <div className="info-row">
                  <strong>Patient:</strong>
                  <span>{selectedApp.patientName}</span>
                </div>
                <div className="info-row">
                  <strong>Hospital:</strong>
                  <span>{selectedApp.hospital}</span>
                </div>
                <div className="info-row">
                  <strong>Status:</strong>
                  <span className={`status-badge status-${selectedApp.status}`}>
                    {selectedApp.status}
                  </span>
                </div>
              </div>

              <div className="documents-grid">
                {selectedApp.declaration && (
                  <a
                    href={`${API_URL}${selectedApp.declaration}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="document-link"
                  >
                    <FileText size={20} />
                    <span>Declaration Form</span>
                    <Download size={16} className="download-icon" />
                  </a>
                )}
                {selectedApp.applicantId && (
                  <a
                    href={`${API_URL}${selectedApp.applicantId}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="document-link"
                  >
                    <FileText size={20} />
                    <span>Applicant ID</span>
                    <Download size={16} className="download-icon" />
                  </a>
                )}
                {selectedApp.staffId && (
                  <a
                    href={`${API_URL}${selectedApp.staffId}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="document-link"
                  >
                    <FileText size={20} />
                    <span>Staff ID</span>
                    <Download size={16} className="download-icon" />
                  </a>
                )}
                {selectedApp.incomeProof && (
                  <a
                    href={`${API_URL}${selectedApp.incomeProof}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="document-link"
                  >
                    <FileText size={20} />
                    <span>Income Proof</span>
                    <Download size={16} className="download-icon" />
                  </a>
                )}
                {selectedApp.photo && (
                  <a
                    href={`${API_URL}${selectedApp.photo}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="document-link"
                  >
                    <FileText size={20} />
                    <span>Photo</span>
                    <Download size={16} className="download-icon" />
                  </a>
                )}
                {selectedApp.hospitalBill && (
                  <a
                    href={`${API_URL}${selectedApp.hospitalBill}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="document-link"
                  >
                    <FileText size={20} />
                    <span>Hospital Bill</span>
                    <Download size={16} className="download-icon" />
                  </a>
                )}
                {selectedApp.reports &&
                  selectedApp.reports.map((rep, idx) => (
                    <a
                      key={idx}
                      href={`${API_URL}${rep}`}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="document-link"
                    >
                      <FileText size={20} />
                      <span>Medical Report {idx + 1}</span>
                      <Download size={16} className="download-icon" />
                    </a>
                  ))}
              </div>
            </div>

            <div className="modal-footer">
              <button className="btn-close" onClick={() => setSelectedApp(null)}>
                Close
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Stats Detail Modal */}
      {selectedStat && (
        <div className="modal-overlay" onClick={() => setSelectedStat(null)}>
          <div className="modal-content stats-modal" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3>
                <FileText size={24} />
                {getStatDetails(selectedStat).title}
              </h3>
            </div>

            <div className="modal-body">
              <div className="stats-summary">
                <div className="summary-item">
                  <div className="summary-label">Total Applications</div>
                  <div className="summary-value">{getStatDetails(selectedStat).filteredData.length}</div>
                </div>
                <div className="summary-item">
                  <div className="summary-label">Percentage</div>
                  <div className="summary-value">
                    {applications.length > 0 
                      ? `${((getStatDetails(selectedStat).filteredData.length / applications.length) * 100).toFixed(1)}%`
                      : '0%'}
                  </div>
                </div>
              </div>

              <div className="applications-list">
                {getStatDetails(selectedStat).filteredData.length > 0 ? (
                  getStatDetails(selectedStat).filteredData.map((app) => (
                    <div key={app._id} className="application-item">
                      <div className="app-item-header">
                        <div className="app-item-title">
                          <strong>{app.applicantName || app.staffName}</strong>
                          <span className={`status-badge status-${app.status}`}>
                            {app.status}
                          </span>
                        </div>
                        <div className="app-item-date">
                          {new Date(app.createdAt).toLocaleDateString("en-US", {
                            month: "short",
                            day: "numeric",
                            year: "numeric",
                          })}
                        </div>
                      </div>
                      <div className="app-item-details">
                        <div className="detail-row">
                          <span className="detail-label">Patient:</span>
                          <span className="detail-value">{app.patientName}</span>
                        </div>
                        <div className="detail-row">
                          <span className="detail-label">Hospital:</span>
                          <span className="detail-value">{app.hospital}</span>
                        </div>
                      </div>
                      <div className="app-item-actions">
                        <button
                          className="btn-view-small"
                          onClick={() => {
                            setSelectedApp(app);
                            setSelectedStat(null);
                          }}
                        >
                          <Eye size={14} />
                          View Details
                        </button>
                        {app.status === "pending" && (
                          <>
                            <button
                              className="btn-verify-small"
                              onClick={() => handleAction(app._id, "verified")}
                            >
                              <CheckCircle size={14} />
                              Verify
                            </button>
                            <button
                              className="btn-reject-small"
                              onClick={() => handleAction(app._id, "rejected")}
                            >
                              <XCircle size={14} />
                              Reject
                            </button>
                          </>
                        )}
                      </div>
                    </div>
                  ))
                ) : (
                  <div className="no-data-content">
                    <FileText size={48} className="no-data-icon" />
                    <p>No applications in this category</p>
                  </div>
                )}
              </div>
            </div>

            <div className="modal-footer">
              <button className="btn-close" onClick={() => setSelectedStat(null)}>
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default StaffVerification;