import io from 'socket.io-client';
import { getSocketUrl } from '../constants/config.js';

let socketInstance = null;

export const initializeSocket = () => {
  if (socketInstance?.connected) {
    return socketInstance;
  }

  socketInstance = io(getSocketUrl(), {
    transports: ['polling', 'websocket'], // Changed order!
    reconnection: true,
    reconnectionDelay: 1000,
    reconnectionDelayMax: 5000,
    reconnectionAttempts: 5,
  });

  socketInstance.on('connect', () => {
    console.log(' Socket connected:', socketInstance.id);
  });

  socketInstance.on('disconnect', () => {
    console.log(' Socket disconnected');
  });

  return socketInstance;
};

export const getSocket = () => {
  if (!socketInstance) {
    return initializeSocket();
  }
  return socketInstance;
};

// Socket event emitters
export const socketEvents = {
  registerRole: (role) => {
    const socket = getSocket();
    socket.emit('registerRole', role);
  },

  registerStaff: (staffId) => {
    const socket = getSocket();
    socket.emit('registerStaff', staffId);
  },

  sendActivityHeartbeat: (userId) => {
    const socket = getSocket();
    if (socket?.connected) {
      socket.emit('activityHeartbeat', userId);
    }
  },

  requestOnlineUsers: () => {
    const socket = getSocket();
    socket.emit('requestOnlineUsers');
  },

  onNewCallRequest: (callback) => {
    const socket = getSocket();
    socket.on('newCallRequest', callback);
  },

  onUserActivityUpdate: (callback) => {
    const socket = getSocket();
    socket.on('userActivityUpdate', callback);
  },

  onOnlineUsersList: (callback) => {
    const socket = getSocket();
    socket.on('onlineUsersList', callback);
  },
};

export default socketInstance;