import axios from 'axios';
import { API_BASE_URL } from '../constants/config.js';

// Axios instance
export const apiClient = axios.create({
  baseURL: `${API_BASE_URL}/api`,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Add token to every request
apiClient.interceptors.request.use((config) => {
  let token = null;
  try {
    token = typeof localStorage !== 'undefined'
      ? localStorage.getItem('token')
      : null;
  } catch (e) {
    console.warn('Failed to read token from storage:', e);
  }

  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Auth API calls
export const authAPI = {
  signIn: async (email, password) => {
    const response = await apiClient.post('/auth/signin', { email, password });
    return response.data;
  },

  resetPassword: (token, newPassword) =>
    apiClient.post('/auth/reset-password', { token, newPassword }),

  forgotPassword: (email) =>
    apiClient.post('/auth/forgot-password', { email }),
};

// User API calls
export const userAPI = {
  getProfile: (userId) => apiClient.get(`/user/profile/${userId}`),
  updateProfile: (userId, data) => apiClient.put(`/user/profile/${userId}`, data),
  uploadProfilePic: (userId, formData) =>
    apiClient.post(`/user/profile/${userId}/upload`, formData, {
      headers: { 'Content-Type': 'multipart/form-data' },
    }),
};

// Staff API calls
export const staffAPI = {
  getProfile: (staffId) => apiClient.get(`/staff/profile/${staffId}`),
  updateProfile: (staffId, data) =>
    apiClient.put(`/staff/profile/${staffId}`, data, {
      headers: { 'Content-Type': 'multipart/form-data' },
    }),
};

// Call Requests API
export const callRequestAPI = {
  create: (data) => apiClient.post('/call-requests', data),
  getAll: () => apiClient.get('/call-requests'),
  getById: (id) => apiClient.get(`/call-requests/${id}`),
  update: (id, data) => apiClient.put(`/call-requests/${id}`, data),
};

// Contact API
export const contactAPI = {
  create: (data) => apiClient.post('/contact', data),
  getAll: () => apiClient.get('/contact'),
};

// Events API
export const eventAPI = {
  getAll: () => apiClient.get('/events'),
  getById: (id) => apiClient.get(`/events/${id}`),
  create: (data) => apiClient.post('/events', data),
};

// Donations API
export const donationAPI = {
  createOrder: (amount, currency = 'INR') =>
    apiClient.post('/donations/create-order', { amount, currency }),

  verifyPayment: (data) =>
    apiClient.post('/donations/verify-payment', data),
};

// Admin API
export const adminAPI = {
  getUsers: () => apiClient.get('/admin/users'),
  getJoinRequests: () => apiClient.get('/admin/join-requests'),
  updateJoinRequest: (id, status) =>
    apiClient.put(`/admin/join-requests/${id}`, { status }),
};

export default apiClient;
