import React, { useState, useRef, useEffect, useCallback } from 'react';
import { Bell } from 'lucide-react';
import axios from 'axios';
import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';
import { API_BASE_URL } from '../shared/constants/config';
import './NotificationBell.css';

const NotificationBell = ({ userRole }) => {
  const [notifications, setNotifications] = useState([]);
  const [unreadCount, setUnreadCount] = useState(0);
  const [isOpen, setIsOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const containerRef = useRef(null);
  const navigate = useNavigate();

  // Fetch notifications
  const fetchNotifications = useCallback(async () => {
    try {
      setLoading(true);
      const token = localStorage.getItem('token');
      if (!token) return;

      const response = await axios.get(`${API_BASE_URL}/api/notifications`, {
        headers: { Authorization: `Bearer ${token}` },
      });

      if (response.data.notifications && Array.isArray(response.data.notifications)) {
        setNotifications(response.data.notifications);
        setUnreadCount(response.data.unreadCount || 0);
      } else if (Array.isArray(response.data)) {
        setNotifications(response.data);
        setUnreadCount(response.data.filter(n => !n.read).length);
      }
    } catch (error) {
      console.error('Error fetching notifications:', error);
    } finally {
      setLoading(false);
    }
  }, []);

  // Mark as read
  const markAsRead = async (notificationId) => {
    try {
      const token = localStorage.getItem('token');
      
      await axios.patch(
        `${API_BASE_URL}/api/notifications/${notificationId}/read`,
        {},
        { headers: { Authorization: `Bearer ${token}` } }
      );

      setNotifications(prev =>
        prev.map(notif =>
          notif._id === notificationId ? { ...notif, read: true } : notif
        )
      );
      setUnreadCount(prev => Math.max(0, prev - 1));
    } catch (error) {
      console.error('Error marking notification as read:', error);
    }
  };

  // Mark all as read
  const markAllAsRead = async (e) => {
    e.stopPropagation();
    try {
      const token = localStorage.getItem('token');
      
      await axios.patch(
        `${API_BASE_URL}/api/notifications/read-all`,
        {},
        { headers: { Authorization: `Bearer ${token}` } }
      );

      setNotifications(prev => prev.map(notif => ({ ...notif, read: true })));
      setUnreadCount(0);
    } catch (error) {
      console.error('Error marking all as read:', error);
    }
  };

  // Handle notification click
  const handleNotificationClick = async (e, notification) => {
    e.stopPropagation();
    
    if (!notification.read) {
      await markAsRead(notification._id);
    }

    setIsOpen(false);

    if (notification.link) {
      navigate(notification.link);
    }
  };

  // Get icon and styling based on notification type
  const getTypeStyle = (type) => {
    switch (type) {
      case 'medical':
      case 'medical_applied':
      case 'medical_verified':
      case 'medical_approved':
      case 'medical_rejected':
      case 'medical_completed':
        return { icon: '', color: '#3B82F6', bg: '#EFF6FF' };
      
      case 'volunteer':
      case 'join_applied':
      case 'join_approved':
      case 'join_rejected':
        return { icon: '', color: '#10B981', bg: '#ECFDF5' };
      
      case 'event':
      case 'event_created':
      case 'event_updated':
      case 'event_reminder':
        return { icon: '', color: '#8B5CF6', bg: '#F5F3FF' };
      
      case 'story':
      case 'story_posted':
      case 'about':
        return { icon: '', color: '#F59E0B', bg: '#FEF3C7' };
      
      case 'contact':
      case 'support':
      case 'contact_replied':
        return { icon: '', color: '#10B981', bg: '#D1FAE5' };
      
      case 'donation':
      case 'donation_received':
        return { icon: '', color: '#EC4899', bg: '#FDF2F8' };
      
      case 'success':
        return { icon: '', color: '#10B981', bg: '#ECFDF5' };
      
      case 'error':
        return { icon: '', color: '#EF4444', bg: '#FEF2F2' };
      
      case 'warning':
        return { icon: '', color: '#F59E0B', bg: '#FFFBEB' };
      
      case 'system':
      case 'admin':
        return { icon: '', color: '#6B7280', bg: '#F3F4F6' };
      
      default:
        return { icon: '', color: '#6B7280', bg: '#F3F4F6' };
    }
  };

  // Get role-specific styling
  const getRoleStyling = () => {
    switch (userRole?.toLowerCase()) {
      case 'admin':
        return { color: '#EF4444', name: 'Admin' };
      case 'staff':
        return { color: '#F59E0B', name: 'Staff' };
      default:
        return { color: '#3B82F6', name: 'User' };
    }
  };

  const roleStyle = getRoleStyling();

  // Handle bell click - simplified
  const handleBellClick = (e) => {
    e.preventDefault();
    e.stopPropagation();
    console.log('Bell clicked, current isOpen:', isOpen);
    console.log('Setting isOpen to:', !isOpen);
    setIsOpen(!isOpen);
  };

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (containerRef.current && !containerRef.current.contains(event.target)) {
        console.log('Clicked outside, closing dropdown');
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
      console.log('Dropdown is open, event listener added');
    }
    
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen]);

  // Log when isOpen changes
  useEffect(() => {
    console.log('isOpen state changed to:', isOpen);
  }, [isOpen]);

  // Fetch notifications on mount and poll every 30 seconds
  useEffect(() => {
    fetchNotifications();
    const interval = setInterval(fetchNotifications, 30000);
    return () => clearInterval(interval);
  }, [fetchNotifications]);

  // Format time ago
  const formatTimeAgo = (date) => {
    const now = new Date();
    const notifDate = new Date(date);
    const diffInSeconds = Math.floor((now - notifDate) / 1000);

    if (diffInSeconds < 60) return 'Just now';
    if (diffInSeconds < 3600) return `${Math.floor(diffInSeconds / 60)}m ago`;
    if (diffInSeconds < 86400) return `${Math.floor(diffInSeconds / 3600)}h ago`;
    if (diffInSeconds < 604800) return `${Math.floor(diffInSeconds / 86400)}d ago`;

    return notifDate.toLocaleDateString();
  };

  console.log('Rendering NotificationBell, isOpen:', isOpen);

  return (
    <div className="notification-bell-container" ref={containerRef}>
      <button
        className="notification-bell-button"
        onClick={handleBellClick}
        type="button"
        aria-label="Notifications"
        aria-expanded={isOpen}
        style={{ borderColor: unreadCount > 0 ? roleStyle.color : 'transparent' }}
      >
        <Bell size={20} color={unreadCount > 0 ? roleStyle.color : '#6B7280'} />
        {unreadCount > 0 && (
          <span 
            className="notification-badge"
            style={{ backgroundColor: roleStyle.color }}
          >
            {unreadCount > 99 ? '99+' : unreadCount}
          </span>
        )}
      </button>
      
      {isOpen && (
        <div 
          className="notification-dropdown" 
          style={{
            display: 'flex',
            opacity: 1,
            visibility: 'visible'
          }}
        >
          <div className="notification-header">
            <div>
              <h3>Notifications</h3>
              <span 
                className="role-badge" 
                style={{ 
                  backgroundColor: roleStyle.color + '20', 
                  color: roleStyle.color 
                }}
              >
                {roleStyle.name}
              </span>
            </div>
            {unreadCount > 0 && (
              <button 
                onClick={markAllAsRead} 
                className="mark-all-read"
                type="button"
                style={{ color: roleStyle.color }}
              >
                Mark all read
              </button>
            )}
          </div>

          {loading && notifications.length === 0 ? (
            <div className="notification-loading">
              <div 
                className="spinner" 
                style={{ borderTopColor: roleStyle.color }}
              ></div>
            </div>
          ) : notifications.length === 0 ? (
            <div className="notification-empty">
              <Bell size={48} strokeWidth={1} color={roleStyle.color} opacity={0.3} />
              <p>No notifications yet</p>
            </div>
          ) : (
            <div className="notification-list">
              {notifications.slice(0, 5).map((notification) => {
                const typeStyle = getTypeStyle(notification.type);
                const isUnread = !notification.read;

                return (
                  <div
                    key={notification._id}
                    className={`notification-item ${isUnread ? 'unread' : ''}`}
                    onClick={(e) => handleNotificationClick(e, notification)}
                  >
                    <div
                      className="notification-icon"
                      style={{ 
                        backgroundColor: typeStyle.bg, 
                        color: typeStyle.color 
                      }}
                    >
                      {typeStyle.icon}
                    </div>
                    <div className="notification-content">
                      <div className="notification-title">
                        {notification.title}
                        {isUnread && (
                          <span 
                            className="unread-dot"
                            style={{ backgroundColor: roleStyle.color }}
                          ></span>
                        )}
                      </div>
                      <p className="notification-message">{notification.message}</p>
                      <span className="notification-time">
                        {formatTimeAgo(notification.createdAt)}
                      </span>
                    </div>
                  </div>
                );
              })}
            </div>
          )}

          <div className="notification-footer">
            <button
              onClick={(e) => {
                e.stopPropagation();
                setIsOpen(false);
                navigate('/notifications');
              }}
              style={{ color: roleStyle.color }}
            >
              View all notifications 
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

NotificationBell.propTypes = {
  userRole: PropTypes.string.isRequired,
};

export default NotificationBell;