import React, { useState, useEffect } from "react";
import axios from "axios";
import "./UserProfile.css";
import { useUser } from '../../shared/contexts/UserContext';
import { User, Mail, Camera, Check, X, Loader } from "lucide-react";
import { API_BASE_URL } from '../../shared/constants/config'; // Add this import

function UserProfile() {
  const { user, setUser } = useUser();
  const [profile, setProfile] = useState({});
  const [image, setImage] = useState(null);
  const [preview, setPreview] = useState(null);
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (!user) return;
    setProfile({
      name: user.name,
      email: user.email,
      profilePic: user.profilePic,
    });
  }, [user]);

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      // Validate file size (max 5MB)
      if (file.size > 5 * 1024 * 1024) {
        setMessage(" Image size should be less than 5MB");
        setTimeout(() => setMessage(""), 3000);
        return;
      }

      // Validate file type
      const validTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/webp'];
      if (!validTypes.includes(file.type)) {
        setMessage(" Please upload a valid image file (JPG, PNG, GIF, or WebP)");
        setTimeout(() => setMessage(""), 3000);
        return;
      }

      setImage(file);
      setPreview(URL.createObjectURL(file));
      setMessage("");
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!user) return setMessage(" User not found.");

    setLoading(true);
    const formData = new FormData();
    if (image) formData.append("profilePic", image);
    formData.append("name", profile.name);
    formData.append("email", profile.email);

    try {
      const res = await axios.put(
        `${API_BASE_URL}/api/user/profile/${user.id}`, // Changed!
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: `Bearer ${user.token}`,
          },
        }
      );

      const updatedUser = res.data.user;

      const profilePicUrl = updatedUser.profilePic
        ? `${API_BASE_URL}/${updatedUser.profilePic.replace(/\\/g, "/")}?t=${Date.now()}` // Changed!
        : null;

      const newUser = { ...user, ...updatedUser, profilePic: profilePicUrl };
      setUser(newUser);
      sessionStorage.setItem("user", JSON.stringify(newUser));

      setProfile({ ...profile, profilePic: profilePicUrl });
      setPreview(null);
      setImage(null);
      setMessage(" Profile updated successfully!");
      
      // Clear success message after 3 seconds
      setTimeout(() => setMessage(""), 3000);
    } catch (err) {
      console.error(" Failed to update user profile:", err);

      if (err.response?.status === 401) {
        setMessage(" Unauthorized. Please log in again.");
      } else if (err.response?.status === 400) {
        setMessage(" Invalid data. Please check your inputs.");
      } else {
        setMessage(" Failed to update profile. Please try again.");
      }
      
      setTimeout(() => setMessage(""), 4000);
    } finally {
      setLoading(false);
    }
  };

  const removePreview = () => {
    setPreview(null);
    setImage(null);
  };

  const handleCancel = () => {
    setProfile({
      name: user.name,
      email: user.email,
      profilePic: user.profilePic,
    });
    setPreview(null);
    setImage(null);
    setMessage("");
  };

  if (!user) {
    return (
      <div className="profile-error">
        <User size={48} />
        <p>Please sign in to view your profile.</p>
      </div>
    );
  }

  return (
    <div className="user-profile-page">
      {/* Rest of your JSX stays the same */}
      <div className="profile-hero">
        <div className="hero-content">
          <h1>User Profile</h1>
          <p>Manage your personal information and preferences</p>
        </div>
      </div>

      <div className="profile-container">
        {message && (
          <div className={`alert ${message.startsWith("") ? "alert-success" : "alert-error"}`}>
            {message.startsWith("") ? <Check size={20} /> : <X size={20} />}
            <span>{message}</span>
          </div>
        )}

        <div className="profile-card">
          <form onSubmit={handleSubmit}>
            <div className="profile-picture-section">
              <div className="picture-wrapper">
                <div className="profile-pic-container">
                  {preview ? (
                    <img src={preview} alt="Preview" className="profile-pic" />
                  ) : profile.profilePic ? (
                    <img src={profile.profilePic} alt="Profile" className="profile-pic" />
                  ) : (
                    <div className="profile-pic-placeholder">
                      <User size={64} />
                    </div>
                  )}
                  <label htmlFor="profile-pic-upload" className="camera-button" title="Upload profile picture">
                    <Camera size={20} />
                    <input
                      id="profile-pic-upload"
                      type="file"
                      accept="image/*"
                      onChange={handleImageChange}
                      style={{ display: "none" }}
                    />
                  </label>
                </div>
                {preview && (
                  <button
                    type="button"
                    className="remove-preview-btn"
                    onClick={removePreview}
                    title="Remove preview"
                  >
                    <X size={16} />
                    Remove
                  </button>
                )}
              </div>
              <div className="picture-info">
                <h3>{profile.name || "User"}</h3>
                <p className="role-badge">User</p>
                <p className="upload-hint">
                  Click the camera icon to upload a new picture (Max 5MB)
                </p>
              </div>
            </div>

            <div className="form-section">
              <h3>Personal Information</h3>
              
              <div className="form-group">
                <label htmlFor="name">
                  <User size={18} />
                  Full Name
                </label>
                <input
                  id="name"
                  type="text"
                  value={profile.name || ""}
                  onChange={(e) => setProfile({ ...profile, name: e.target.value })}
                  placeholder="Enter your full name"
                  required
                  minLength={2}
                  maxLength={50}
                />
              </div>

              <div className="form-group">
                <label htmlFor="email">
                  <Mail size={18} />
                  Email Address
                </label>
                <input
                  id="email"
                  type="email"
                  value={profile.email || ""}
                  onChange={(e) => setProfile({ ...profile, email: e.target.value })}
                  placeholder="Enter your email"
                  required
                />
              </div>
            </div>

            <div className="form-actions">
              <button
                type="button"
                className="btn-cancel"
                onClick={handleCancel}
                disabled={loading}
              >
                <X size={18} />
                Cancel
              </button>
              <button
                type="submit"
                className="btn-submit"
                disabled={loading}
              >
                {loading ? (
                  <>
                    <Loader size={18} className="spinner" />
                    Updating...
                  </>
                ) : (
                  <>
                    <Check size={18} />
                    Update Profile
                  </>
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default UserProfile;