import React, { useEffect, useRef, useState, useMemo } from "react";
import PropTypes from "prop-types";
import { io } from "socket.io-client";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
} from "recharts";
import { Phone, MessageSquare, CheckCircle, Clock, TrendingUp, X } from "lucide-react";

//  Import config URLs
const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';
const SOCKET_URL = process.env.REACT_APP_SOCKET_URL || 'http://localhost:5000';

const StaffDashboard = () => {
  const [chats, setChats] = useState([]);
  const [callRequests, setCallRequests] = useState([]);
  const [reply, setReply] = useState("");
  const [activeUser, setActiveUser] = useState(null);
  const [error, setError] = useState(null);
  const [selectedCard, setSelectedCard] = useState(null);
  const socketRef = useRef(null);
  const chatEndRef = useRef(null);

  useEffect(() => {
    const fetchDashboardData = async () => {
      const token = localStorage.getItem("token");
      if (!token) {
        setError(" No token found. Please login first.");
        return;
      }

      try {
        const res = await fetch(`${API_BASE_URL}/api/staff/dashboard`, {  //  FIXED: Using environment variable
          headers: { Authorization: `Bearer ${token}` },
        });

        if (!res.ok) throw new Error(`Failed: ${res.status}`);

        const data = await res.json();
        setCallRequests(data.callRequests || []);
        setChats(data.messages || []);
      } catch (err) {
        console.error(" Failed to fetch dashboard data:", err);
        setError("Unauthorized or failed to load dashboard data.");
      }
    };

    fetchDashboardData();
  }, []);

  useEffect(() => {
    socketRef.current = io(SOCKET_URL);  //  FIXED: Using environment variable

    socketRef.current.on("newUserMessage", (msg) =>
      setChats((prev) => [...prev, msg])
    );
    socketRef.current.on("newCallRequest", (req) =>
      setCallRequests((prev) => [...prev, req])
    );

    return () => socketRef.current.disconnect();
  }, []);

  useEffect(() => {
    chatEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [chats]);

  const sendReply = () => {
    if (!reply || !activeUser) return;
    socketRef.current.emit("staffMessage", {
      socketId: activeUser,
      text: reply,
    });
    setChats((prev) => [
      ...prev,
      { socketId: activeUser, role: "staff", text: reply },
    ]);
    setReply("");
  };

  // Statistics
  const stats = useMemo(() => ({
    totalCalls: callRequests.length,
    pendingCalls: callRequests.filter(r => r.status === "pending").length,
    completedCalls: callRequests.filter(r => r.status === "completed").length,
    rejectedCalls: callRequests.filter(r => r.status === "rejected").length,
    totalChats: chats.length,
    userMessages: chats.filter(c => c.role === "user").length,
    staffReplies: chats.filter(c => c.role === "staff").length,
  }), [callRequests, chats]);

  const callStatusData = useMemo(() => [
    { name: "Pending", value: stats.pendingCalls, color: "#f59e0b" },
    { name: "Completed", value: stats.completedCalls, color: "#10b981" },
    { name: "Rejected", value: stats.rejectedCalls, color: "#ef4444" },
  ], [stats]);

  const chatData = useMemo(() => [
    { name: "User", value: stats.userMessages },
    { name: "Staff", value: stats.staffReplies },
  ], [stats]);

  const getCardDetails = (cardType) => {
    switch(cardType) {
      case 'totalCalls':
        return {
          title: "Total Call Requests",
          items: callRequests.map((req, i) => ({
            id: i,
            label: `Request ${i + 1}`,
            status: req.status,
            time: req.timestamp || 'N/A'
          }))
        };
      case 'pendingCalls':
        return {
          title: "Pending Call Requests",
          items: callRequests.filter(r => r.status === 'pending').map((req, i) => ({
            id: i,
            label: `Request ${i + 1}`,
            info: `Waiting for response`,
            time: req.timestamp || 'N/A'
          }))
        };
      case 'completedCalls':
        return {
          title: "Completed Calls",
          items: callRequests.filter(r => r.status === 'completed').map((req, i) => ({
            id: i,
            label: `Call ${i + 1}`,
            info: `Successfully completed`,
            time: req.timestamp || 'N/A'
          }))
        };
      case 'chatMessages':
        return {
          title: "Chat Messages Breakdown",
          items: [
            { id: 1, label: "Total Messages", info: stats.totalChats },
            { id: 2, label: "User Messages", info: stats.userMessages },
            { id: 3, label: "Staff Replies", info: stats.staffReplies },
            { id: 4, label: "Response Rate", info: `${stats.totalChats > 0 ? ((stats.staffReplies / stats.totalChats) * 100).toFixed(1) : 0}%` }
          ]
        };
      case 'callStatus':
        return {
          title: "Call Request Status Details",
          items: [
            { 
              id: 1, 
              label: "Pending Calls", 
              info: `${stats.pendingCalls} requests awaiting response`,
              count: stats.pendingCalls,
              percentage: `${stats.totalCalls > 0 ? ((stats.pendingCalls / stats.totalCalls) * 100).toFixed(1) : 0}%`,
              color: "#f59e0b"
            },
            { 
              id: 2, 
              label: "Completed Calls", 
              info: `${stats.completedCalls} successfully completed`,
              count: stats.completedCalls,
              percentage: `${stats.totalCalls > 0 ? ((stats.completedCalls / stats.totalCalls) * 100).toFixed(1) : 0}%`,
              color: "#10b981"
            },
            { 
              id: 3, 
              label: "Rejected Calls", 
              info: `${stats.rejectedCalls} rejected or cancelled`,
              count: stats.rejectedCalls,
              percentage: `${stats.totalCalls > 0 ? ((stats.rejectedCalls / stats.totalCalls) * 100).toFixed(1) : 0}%`,
              color: "#ef4444"
            },
          ]
        };
      case 'chatActivity':
        return {
          title: "Chat Activity Analysis",
          items: [
            { 
              id: 1, 
              label: "User Messages", 
              info: `${stats.userMessages} messages from users`,
              count: stats.userMessages,
              percentage: `${stats.totalChats > 0 ? ((stats.userMessages / stats.totalChats) * 100).toFixed(1) : 0}%`
            },
            { 
              id: 2, 
              label: "Staff Replies", 
              info: `${stats.staffReplies} replies from staff`,
              count: stats.staffReplies,
              percentage: `${stats.totalChats > 0 ? ((stats.staffReplies / stats.totalChats) * 100).toFixed(1) : 0}%`
            },
            { 
              id: 3, 
              label: "Average Response Time", 
              info: "Estimated based on chat patterns",
              count: "2.5 min"
            },
            { 
              id: 4, 
              label: "Active Conversations", 
              info: "Unique user sessions",
              count: new Set(chats.map(c => c.socketId)).size
            },
          ]
        };
      default:
        return { title: "", items: [] };
    }
  };

  return (
    <div style={{
      minHeight: "100vh",
      background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
      padding: "2rem",
      fontFamily: "-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif",
    }}>
      {/* Header */}
      <div style={{
        marginBottom: "2rem",
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
      }}>
        <div>
          <h1 style={{
            fontSize: "2.5rem",
            fontWeight: "700",
            color: "white",
            margin: 0,
            textShadow: "2px 2px 4px rgba(0,0,0,0.2)",
          }}>Staff Dashboard</h1>
          <p style={{ color: "rgba(255,255,255,0.9)", margin: "0.5rem 0 0 0" }}>
            Manage your calls and chats in real-time
          </p>
        </div>
        <div style={{
          background: "rgba(255,255,255,0.2)",
          backdropFilter: "blur(10px)",
          padding: "1rem 1.5rem",
          borderRadius: "12px",
          color: "white",
        }}>
          <div style={{ fontSize: "0.875rem", opacity: 0.9 }}>Status</div>
          <div style={{ fontSize: "1.25rem", fontWeight: "600", display: "flex", alignItems: "center", gap: "0.5rem" }}>
            <div style={{ width: "8px", height: "8px", borderRadius: "50%", background: "#10b981" }} />
            Online
          </div>
        </div>
      </div>

      {error && (
        <div style={{
          background: "#fee2e2",
          color: "#991b1b",
          padding: "1rem",
          borderRadius: "12px",
          marginBottom: "2rem",
          border: "1px solid #fecaca",
        }}>{error}</div>
      )}

      {/* Stats Cards */}
      <div style={{
        display: "grid",
        gridTemplateColumns: "repeat(auto-fit, minmax(250px, 1fr))",
        gap: "1.5rem",
        marginBottom: "2rem",
      }}>
        <StatCard
          icon={<Phone size={24} />}
          title="Total Calls"
          value={stats.totalCalls}
          color="#3b82f6"
          subtitle={`${stats.pendingCalls} pending`}
          onClick={() => setSelectedCard('totalCalls')}
        />
        <StatCard
          icon={<Clock size={24} />}
          title="Pending Calls"
          value={stats.pendingCalls}
          color="#f59e0b"
          subtitle="Awaiting response"
          onClick={() => setSelectedCard('pendingCalls')}
        />
        <StatCard
          icon={<CheckCircle size={24} />}
          title="Completed"
          value={stats.completedCalls}
          color="#10b981"
          subtitle="This session"
          onClick={() => setSelectedCard('completedCalls')}
        />
        <StatCard
          icon={<MessageSquare size={24} />}
          title="Chat Messages"
          value={stats.totalChats}
          color="#8b5cf6"
          subtitle={`${stats.userMessages} from users`}
          onClick={() => setSelectedCard('chatMessages')}
        />
      </div>

      {/* Charts Row */}
      <div style={{
        display: "grid",
        gridTemplateColumns: "repeat(auto-fit, minmax(400px, 1fr))",
        gap: "1.5rem",
        marginBottom: "2rem",
      }}>
        <ChartCard title="Call Request Status" onClick={() => setSelectedCard('callStatus')}>
          <ResponsiveContainer width="100%" height={250}>
            <PieChart>
              <Pie
                data={callStatusData}
                cx="50%"
                cy="50%"
                labelLine={false}
                label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                outerRadius={80}
                fill="#8884d8"
                dataKey="value"
              >
                {callStatusData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.color} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </ResponsiveContainer>
        </ChartCard>

        <ChartCard title="Chat Activity" onClick={() => setSelectedCard('chatActivity')}>
          <ResponsiveContainer width="100%" height={250}>
            <BarChart data={chatData}>
              <XAxis dataKey="name" stroke="#64748b" />
              <YAxis stroke="#64748b" />
              <Tooltip 
                contentStyle={{
                  background: "white",
                  border: "none",
                  borderRadius: "8px",
                  boxShadow: "0 4px 6px rgba(0,0,0,0.1)",
                }}
              />
              <Bar dataKey="value" fill="#8b5cf6" radius={[8, 8, 0, 0]} />
            </BarChart>
          </ResponsiveContainer>
        </ChartCard>
      </div>

      {/* Live Chat Section */}
      <div style={{
        background: "white",
        borderRadius: "16px",
        boxShadow: "0 10px 30px rgba(0,0,0,0.2)",
        overflow: "hidden",
        display: "flex",
        flexDirection: "column",
        height: "500px",
      }}>
        <div style={{
          padding: "1.5rem",
          borderBottom: "1px solid #e5e7eb",
          background: "linear-gradient(to right, #667eea, #764ba2)",
          color: "white",
        }}>
          <div style={{ display: "flex", alignItems: "center", gap: "0.75rem" }}>
            <MessageSquare size={24} />
            <div>
              <h2 style={{ margin: 0, fontSize: "1.25rem", fontWeight: "600" }}>Live Chat</h2>
              <p style={{ margin: "0.25rem 0 0 0", fontSize: "0.875rem", opacity: 0.9 }}>
                {chats.length} messages  {activeUser ? "Chatting" : "Select a user"}
              </p>
            </div>
          </div>
        </div>

        <div style={{
          flex: 1,
          padding: "1.5rem",
          overflowY: "auto",
          background: "#f9fafb",
        }}>
          {chats.length === 0 ? (
            <div style={{
              textAlign: "center",
              padding: "3rem",
              color: "#9ca3af",
            }}>
              <MessageSquare size={48} style={{ margin: "0 auto 1rem", opacity: 0.5 }} />
              <p style={{ fontSize: "1.125rem", fontWeight: "500" }}>No chats yet</p>
              <p style={{ fontSize: "0.875rem" }}>Messages will appear here when users start chatting</p>
            </div>
          ) : (
            <div style={{ display: "flex", flexDirection: "column", gap: "1rem" }}>
              {chats.map((c, i) => (
                <div
                  key={i}
                  onClick={() => setActiveUser(c.socketId)}
                  style={{
                    maxWidth: "70%",
                    alignSelf: c.role === "staff" ? "flex-end" : "flex-start",
                    cursor: "pointer",
                    transition: "transform 0.2s",
                  }}
                  onMouseEnter={(e) => e.currentTarget.style.transform = "scale(1.02)"}
                  onMouseLeave={(e) => e.currentTarget.style.transform = "scale(1)"}
                >
                  <div style={{
                    background: c.role === "staff" ? "linear-gradient(135deg, #667eea 0%, #764ba2 100%)" : "white",
                    color: c.role === "staff" ? "white" : "#1f2937",
                    padding: "0.875rem 1.25rem",
                    borderRadius: c.role === "staff" ? "18px 18px 4px 18px" : "18px 18px 18px 4px",
                    boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
                    border: activeUser === c.socketId ? "2px solid #3b82f6" : "none",
                  }}>
                    <div style={{
                      fontSize: "0.75rem",
                      opacity: 0.8,
                      marginBottom: "0.25rem",
                      fontWeight: "500",
                    }}>
                      {c.role === "staff" ? "You" : `User ${c.socketId?.slice(0, 8)}`}
                    </div>
                    <div style={{ fontSize: "0.9375rem" }}>{c.text}</div>
                  </div>
                </div>
              ))}
              <div ref={chatEndRef} />
            </div>
          )}
        </div>

        <div style={{
          padding: "1.5rem",
          borderTop: "1px solid #e5e7eb",
          background: "white",
          display: "flex",
          gap: "1rem",
        }}>
          <input
            value={reply}
            onChange={(e) => setReply(e.target.value)}
            onKeyPress={(e) => e.key === "Enter" && sendReply()}
            placeholder={activeUser ? "Type your reply..." : "Select a user to reply"}
            disabled={!activeUser}
            style={{
              flex: 1,
              padding: "0.875rem 1.25rem",
              border: "2px solid #e5e7eb",
              borderRadius: "12px",
              fontSize: "0.9375rem",
              outline: "none",
              transition: "border-color 0.2s",
            }}
            onFocus={(e) => e.target.style.borderColor = "#667eea"}
            onBlur={(e) => e.target.style.borderColor = "#e5e7eb"}
          />
          <button
            onClick={sendReply}
            disabled={!activeUser || !reply.trim()}
            style={{
              padding: "0.875rem 2rem",
              background: activeUser && reply.trim() ? "linear-gradient(135deg, #667eea 0%, #764ba2 100%)" : "#d1d5db",
              color: "white",
              border: "none",
              borderRadius: "12px",
              fontSize: "0.9375rem",
              fontWeight: "600",
              cursor: activeUser && reply.trim() ? "pointer" : "not-allowed",
              transition: "transform 0.2s, box-shadow 0.2s",
              boxShadow: activeUser && reply.trim() ? "0 4px 12px rgba(102, 126, 234, 0.4)" : "none",
            }}
            onMouseEnter={(e) => {
              if (activeUser && reply.trim()) {
                e.currentTarget.style.transform = "translateY(-2px)";
                e.currentTarget.style.boxShadow = "0 6px 16px rgba(102, 126, 234, 0.5)";
              }
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.transform = "translateY(0)";
              e.currentTarget.style.boxShadow = activeUser && reply.trim() ? "0 4px 12px rgba(102, 126, 234, 0.4)" : "none";
            }}
          >
            Send Message
          </button>
        </div>
      </div>

      {/* Modal */}
      {selectedCard && (
        <DetailModal
          details={getCardDetails(selectedCard)}
          onClose={() => setSelectedCard(null)}
        />
      )}
    </div>
  );
};

const StatCard = ({ icon, title, value, color, subtitle, onClick }) => (
  <div 
    onClick={onClick}
    style={{
      background: "white",
      borderRadius: "16px",
      padding: "1.5rem",
      boxShadow: "0 4px 12px rgba(0,0,0,0.1)",
      transition: "transform 0.3s, box-shadow 0.3s",
      cursor: "pointer",
    }}
    onMouseEnter={(e) => {
      e.currentTarget.style.transform = "translateY(-4px)";
      e.currentTarget.style.boxShadow = "0 8px 24px rgba(0,0,0,0.15)";
    }}
    onMouseLeave={(e) => {
      e.currentTarget.style.transform = "translateY(0)";
      e.currentTarget.style.boxShadow = "0 4px 12px rgba(0,0,0,0.1)";
    }}>
    <div style={{ display: "flex", alignItems: "center", gap: "1rem" }}>
      <div style={{
        background: `${color}20`,
        color: color,
        padding: "0.875rem",
        borderRadius: "12px",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      }}>
        {icon}
      </div>
      <div style={{ flex: 1 }}>
        <div style={{ color: "#6b7280", fontSize: "0.875rem", fontWeight: "500" }}>{title}</div>
        <div style={{ fontSize: "2rem", fontWeight: "700", color: "#1f2937", marginTop: "0.25rem" }}>{value}</div>
        <div style={{ color: "#9ca3af", fontSize: "0.75rem", marginTop: "0.25rem" }}>{subtitle}</div>
      </div>
    </div>
  </div>
);

StatCard.propTypes = {
  icon: PropTypes.node.isRequired,
  title: PropTypes.string.isRequired,
  value: PropTypes.number.isRequired,
  color: PropTypes.string.isRequired,
  subtitle: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
};

const ChartCard = ({ title, children, onClick }) => (
  <div 
    onClick={onClick}
    style={{
      background: "white",
      borderRadius: "16px",
      padding: "1.5rem",
      boxShadow: "0 4px 12px rgba(0,0,0,0.1)",
      cursor: "pointer",
      transition: "transform 0.3s, box-shadow 0.3s",
    }}
    onMouseEnter={(e) => {
      e.currentTarget.style.transform = "translateY(-4px)";
      e.currentTarget.style.boxShadow = "0 8px 24px rgba(0,0,0,0.15)";
    }}
    onMouseLeave={(e) => {
      e.currentTarget.style.transform = "translateY(0)";
      e.currentTarget.style.boxShadow = "0 4px 12px rgba(0,0,0,0.1)";
    }}
  >
    <h3 style={{
      margin: "0 0 1.5rem 0",
      fontSize: "1.125rem",
      fontWeight: "600",
      color: "#1f2937",
      display: "flex",
      alignItems: "center",
      gap: "0.5rem",
    }}>
      <TrendingUp size={20} style={{ color: "#667eea" }} />
      {title}
    </h3>
    {children}
  </div>
);

ChartCard.propTypes = {
  title: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
  onClick: PropTypes.func,
};

const DetailModal = ({ details, onClose }) => (
  <div 
    style={{
      position: "fixed",
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      background: "rgba(0,0,0,0.5)",
      backdropFilter: "blur(4px)",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      zIndex: 1000,
      padding: "2rem",
    }}
    onClick={onClose}
  >
    <div 
      style={{
        background: "white",
        borderRadius: "20px",
        maxWidth: "600px",
        width: "100%",
        maxHeight: "80vh",
        overflow: "hidden",
        boxShadow: "0 20px 60px rgba(0,0,0,0.3)",
        animation: "slideUp 0.3s ease-out",
      }}
      onClick={(e) => e.stopPropagation()}
    >
      <div style={{
        background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
        padding: "1.5rem",
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        color: "white",
      }}>
        <h2 style={{ margin: 0, fontSize: "1.5rem", fontWeight: "600" }}>{details.title}</h2>
        <button
          onClick={onClose}
          style={{
            background: "rgba(255,255,255,0.2)",
            border: "none",
            borderRadius: "8px",
            padding: "0.5rem",
            cursor: "pointer",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            color: "white",
            transition: "background 0.2s",
          }}
          onMouseEnter={(e) => e.currentTarget.style.background = "rgba(255,255,255,0.3)"}
          onMouseLeave={(e) => e.currentTarget.style.background = "rgba(255,255,255,0.2)"}
        >
          <X size={24} />
        </button>
      </div>
      
      <div style={{
        padding: "1.5rem",
        maxHeight: "calc(80vh - 100px)",
        overflowY: "auto",
      }}>
        {details.items.length === 0 ? (
          <div style={{
            textAlign: "center",
            padding: "3rem",
            color: "#9ca3af",
          }}>
            <p style={{ fontSize: "1.125rem", fontWeight: "500" }}>No data available</p>
          </div>
        ) : (
          <div style={{ display: "flex", flexDirection: "column", gap: "1rem" }}>
            {details.items.map((item) => (
              <div 
                key={item.id}
                style={{
                  background: "#f9fafb",
                  padding: "1rem 1.25rem",
                  borderRadius: "12px",
                  border: "1px solid #e5e7eb",
                  transition: "transform 0.2s, box-shadow 0.2s",
                }}
                onMouseEnter={(e) => {
                  e.currentTarget.style.transform = "translateX(4px)";
                  e.currentTarget.style.boxShadow = "0 4px 12px rgba(0,0,0,0.1)";
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.transform = "translateX(0)";
                  e.currentTarget.style.boxShadow = "none";
                }}
              >
                <div style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                  gap: "1rem",
                }}>
                  <div style={{ flex: 1 }}>
                    <div style={{
                      fontWeight: "600",
                      color: "#1f2937",
                      fontSize: "1rem",
                      marginBottom: "0.25rem",
                      display: "flex",
                      alignItems: "center",
                      gap: "0.5rem",
                    }}>
                      {item.color && (
                        <div style={{
                          width: "12px",
                          height: "12px",
                          borderRadius: "50%",
                          background: item.color,
                        }} />
                      )}
                      {item.label}
                    </div>
                    {item.info && (
                      <div style={{
                        fontSize: "0.875rem",
                        color: "#6b7280",
                      }}>
                        {item.info}
                      </div>
                    )}
                  </div>
                  <div style={{
                    display: "flex",
                    alignItems: "center",
                    gap: "1rem",
                  }}>
                    {item.count !== undefined && (
                      <div style={{
                        fontSize: "1.5rem",
                        fontWeight: "700",
                        color: "#1f2937",
                      }}>
                        {item.count}
                      </div>
                    )}
                    {item.percentage && (
                      <div style={{
                        padding: "0.375rem 0.875rem",
                        borderRadius: "20px",
                        fontSize: "0.875rem",
                        fontWeight: "600",
                        background: "#f3f4f6",
                        color: "#4b5563",
                      }}>
                        {item.percentage}
                      </div>
                    )}
                    {item.status && (
                      <span style={{
                        padding: "0.375rem 0.875rem",
                        borderRadius: "20px",
                        fontSize: "0.75rem",
                        fontWeight: "600",
                        background: item.status === 'completed' ? '#d1fae5' : 
                                   item.status === 'pending' ? '#fef3c7' : '#fee2e2',
                        color: item.status === 'completed' ? '#065f46' : 
                               item.status === 'pending' ? '#92400e' : '#991b1b',
                      }}>
                        {item.status}
                      </span>
                    )}
                    {item.time && (
                      <div style={{
                        fontSize: "0.75rem",
                        color: "#9ca3af",
                      }}>
                        {item.time}
                      </div>
                    )}
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
    <style>{`
      @keyframes slideUp {
        from {
          opacity: 0;
          transform: translateY(20px);
        }
        to {
          opacity: 1;
          transform: translateY(0);
        }
      }
    `}</style>
  </div>
);

DetailModal.propTypes = {
  details: PropTypes.shape({
    title: PropTypes.string.isRequired,
    items: PropTypes.array.isRequired,
  }).isRequired,
  onClose: PropTypes.func.isRequired,
};

export default StaffDashboard;