import React, {
  createContext,
  useContext,
  useState,
  useMemo,
  useCallback,
  useEffect,
} from "react";
import PropTypes from "prop-types";
import { authAPI, userAPI, staffAPI } from "../services/api.js";
import { socketEvents, initializeSocket } from "../services/socketService.js";

import { API_BASE_URL } from '../constants/config';
const UserContext = createContext();

export function useUser() {
  const context = useContext(UserContext);
  if (!context) throw new Error("useUser must be used within UserProvider");
  return context;
}

function normalizePic(path, version) {
  if (!path) return null;
  const base = `${API_BASE_URL}/${path.replace(/\\/g, "/")}`;
  return version ? `${base}?v=${version}` : base;
}

function getStorageItem(key) {
  try {
    if (typeof window !== "undefined" && localStorage) return localStorage.getItem(key);
    if (typeof sessionStorage !== "undefined") return sessionStorage.getItem(key);
  } catch (e) {
    console.warn("Storage access failed:", e);
  }
  return null;
}

function setStorageItem(key, value) {
  try {
    if (typeof window !== "undefined" && localStorage) localStorage.setItem(key, value);
    if (typeof sessionStorage !== "undefined") sessionStorage.setItem(key, value);
  } catch (e) {
    console.warn("Storage write failed:", e);
  }
}

function removeStorageItem(key) {
  try {
    if (typeof window !== "undefined" && localStorage) localStorage.removeItem(key);
    if (typeof sessionStorage !== "undefined") sessionStorage.removeItem(key);
  } catch (e) {
    console.warn("Storage remove failed:", e);
  }
}

export function UserProvider({ children }) {
  const [user, setUser] = useState(() => {
    const stored = getStorageItem("user");
    if (!stored) return null;
    try {
      const parsed = JSON.parse(stored);
      return parsed.profilePic
        ? { ...parsed, profilePic: parsed.profilePic.replace(/\\/g, "/") }
        : parsed;
    } catch (e) {
      console.error("Failed to parse stored user:", e);
      return null;
    }
  });

  // Initialize socket when user changes
  useEffect(() => {
    if (!user) return;
    initializeSocket();
    socketEvents.registerRole(user.role);
    if (user.role === "staff") socketEvents.registerStaff(user.id);
  }, [user]);

  // Sign out user
  const signOut = useCallback(() => {
    setUser(null);
    removeStorageItem("user");
    removeStorageItem("token");
  }, []);

  // Sign in user
  const signIn = useCallback(async (email, password, role) => {
    try {
      const data = await authAPI.signIn(email, password);
      if (!data.user) return false;

      if (role !== data.user.role) {
        console.error(`Role mismatch. Expected ${role}, got ${data.user.role}`);
        return false;
      }

      //  CRITICAL: Set token FIRST before making any other API calls
      setStorageItem("token", data.token);

      let profilePicUrl =
        data.user.profilePic && data.user.profilePic !== ""
          ? normalizePic(data.user.profilePic, data.user.profilePicUpdatedAt || data.user.updatedAt)
          : null;

      // Fetch staff profile if role is staff
      if (data.user.role === "staff") {
        try {
          const res = await staffAPI.getProfile(data.user._id);
          if (res.data.profilePic) {
            profilePicUrl = normalizePic(res.data.profilePic, res.data.profilePicUpdatedAt || res.data.updatedAt);
          }
        } catch (err) {
          console.error("Failed to fetch staff profile:", err.message);
          // Don't fail login if profile fetch fails
        }
      }

      const userData = {
        id: data.user._id,
        name: data.user.name,
        email: data.user.email,
        role: data.user.role,
        profilePic: profilePicUrl,
        token: data.token,
      };

      setUser(userData);
      setStorageItem("user", JSON.stringify(userData));

      initializeSocket();
      socketEvents.registerRole(userData.role);
      if (userData.role === "staff") socketEvents.registerStaff(userData.id);

      return true;
    } catch (error) {
      console.error("Login error:", error);
      return false;
    }
  }, []);

  // Refresh user profile from backend
  const refreshUser = useCallback(async () => {
    const token = getStorageItem("token");
    
    // Debug logging
    console.log(" Token in storage:", token ? "EXISTS" : "MISSING");
    
    if (!token) {
      console.warn("No token found, signing out");
      signOut();
      return;
    }

    const stored = getStorageItem("user");
    const current = stored ? JSON.parse(stored) : null;
    
    console.log(" Current user:", current);
    
    if (!current?.id) {
      console.warn("No user ID found, signing out");
      signOut();
      return;
    }

    try {
      console.log(" Fetching profile for:", current.role, current.id);
      
      const res = current.role === "staff"
        ? await staffAPI.getProfile(current.id)
        : await userAPI.getProfile(current.id);

      console.log(" Profile fetched successfully");

      const nextProfilePic = res.data.profilePic
        ? normalizePic(res.data.profilePic, res.data.profilePicUpdatedAt || res.data.updatedAt)
        : null;

      setUser((prev) => {
        const noChange =
          prev &&
          prev.name === res.data.name &&
          prev.email === res.data.email &&
          prev.role === res.data.role &&
          prev.profilePic === nextProfilePic;

        if (noChange) {
          console.log(" No profile changes detected");
          return prev;
        }

        console.log(" Updating user profile");
        
        const updatedUser = {
          ...(prev || {}),
          id: current.id,
          name: res.data.name,
          email: res.data.email,
          role: res.data.role,
          profilePic: nextProfilePic,
          token: current.token,
        };

        setStorageItem("user", JSON.stringify(updatedUser));
        return updatedUser;
      });
    } catch (err) {
      console.error(" Failed to refresh user:", err.message);
      
      // If 401 Unauthorized, logout
      if (err.response?.status === 401) {
        console.warn("Token expired or invalid, signing out");
        signOut();
      }
    }
  }, [signOut]);

  const value = useMemo(
    () => ({ user, signIn, signOut, setUser, refreshUser }),
    [user, signIn, signOut, refreshUser]
  );

  return <UserContext.Provider value={value}>{children}</UserContext.Provider>;
}

UserProvider.propTypes = {
  children: PropTypes.node.isRequired,
};